import './App.css';
import React, {useState, useEffect} from 'react';

function App() 
{
	const [message, setMessage] = useState(null);

	useEffect(() => {
		fetch("/hello")
		.then(response => response.json())
		.then(data => setMessage(data.message))
	},[])
	

  	return (
    	<div className="App">
      		<p>{message}</p>
    	</div>
  	);
}

export default App;
